<template>
  <v-row
    justify="space-around"
    align="center"
  >
    <v-color-picker
      v-model="picker"
      flat
    ></v-color-picker>

    <v-color-picker
      v-model="picker"
      elevation="15"
    ></v-color-picker>
  </v-row>
</template>

<script>
export default {
  name: 'Media',

  data: () => ({
    picker: null,
  }),
  computed: {
    color () {
      switch (this.value) {
          case 0: return 'purple'
          case 1: return 'pink'
          case 2: return 'green'
          default: return 'pink'
          }
        },
      },
};

</script>
